.Protesis_header{
    width: 100%;
    min-height: 60vh;
    background-image: url("../img/protesis.jpg");
    background-position: top;
    display: flex;
    justify-content: center;
    align-items: center;

}
.Protesis-h2{
    background-color: rgb(255, 255, 255,0.4);
    font-size: 2em;
    text-align: center;

}
/* ---------------------------- */
section{
    font-size: 1rem;
    width: 100%;
    display: flex;
}
section .img {
    width: 50%;
    height: 100vh;
    position: sticky;
    top: 0;
    overflow: hidden;
    z-index: -1;
    
}
section .img  img{
    width: 100%;
    object-fit: cover;
    
}
section .text {
    width: 50%;
    height: auto;
    padding:2em ;
}
section .text h3{
    font-size: 3em;
    margin: 1rem;
}
section .text p{
    margin-top: .5em;
    font-size: 1.5em;
}
section .text h4{ 
    margin-top: 1em;
    font-size: 2em;
}
@media (max-width: 1024px) {
    section{
       
        flex-direction: column;
        
    }
    section .text {
        background-color: white;
        
    }
}
@media (max-width: 480px) {
    section{
       
        flex-direction: column;
    }
    section .img {
        width: 100%;
        height: 50vh;
        position: relative;
       
    }
    section .text {
        background-color: white;
        width: 100%;
        height: auto;
        padding:0 ;
    }
    section .text h3{
        
        font-size: 1.5em;
        margin: 1rem;
    }
    section .text p{
        margin-top: .5em;
        font-size: 1.5em;
    }
    section .text h4{ 
        margin-top: 1em;
        font-size: 1.5em;
    }
    /* -------------------------- */
    .section{
        position: relative;
        font-size: 1rem;
        width: 100%;
        min-height: 25em;
        background-image: url(../img/dienteConPlaca.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        z-index: -1;
    }
    .section::after{
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: radial-gradient(circle, rgba(19,19,19,0) 5%, rgba(251,251,251,0) 88%);
    }
    .section__div{
        padding: .5rem;
        font-size: .7em;
        position: relative;
        z-index: 9;
        width: 100%;
        min-height: 20rem;
        color: rgb(0, 0, 0);
        
    }
    
    .section__div--p{
        margin-top: .5em;
    }
}