.BocaSeca__header{
    font-size: 1rem;
    width: 100%;
    height: 60vh;
    background-image: url(../img/BocaSeca1.jpg);
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    position: relative;
    z-index: -1;
    display: flex;
    justify-content: flex-end;
    align-items:center;

}
.BocaSeca__header::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, rgba(252,252,252,0) 33%, rgba(135,13,67,1) 91%);
   

}
.BocaSeca__header-h2{
    width: 30rem;

    color: white;
    position: relative;
    z-index: 1;
    padding-right: 1rem;
    font-size: 2em;
}
/*--------------------*/
.BocaSeca__section{
    font-size: 1rem;
    padding: 2em;
    background-color: #870d43;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.BocaSeca__section--h2{
    font-size: 2em;
    text-align: center;
  
}
.BocaSeca__section--p{
    text-align: center;
    font-size: 1.5em;
    color: antiquewhite;
}

/*-------------------------------------*/
.carrousel{

    font-size: 1rem;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #870d43;

}

.conteCarrousel{
    width: 50em;
    height:30em;
    overflow: hidden;
    border-radius: 50px;
background: #ebb2b2;
box-shadow:  20px 20px 60px #c89797,
             -20px -20px 60px #ffcdcd;
}

.itemCarrousel{
    position: relative;
    width: 100%;
    height: 100%;
}

.itemCarrouselTarjeta{
    
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 1em;

}
.itemCarrouselTarjeta--h2{
    background-color: rgb(255, 255, 255,.5);
    padding: .5rem;
    text-transform: capitalize;
    font-size: 2.5em;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
}

.itemCarrouselArrows{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%; 
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px; 
}

.itemCarrouselArrows > i:hover {
    cursor: pointer;
}

#itemCarrousel-1{
    width: 100%;
    height: 100%;
    background-image: url("../img/BocaSeca3.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: red;

}

#itemCarrousel-2{
    width: 100%;
    height: 100%;
    background-image: url("../img/BocaSeca4.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: rgb(50, 200, 226);
}

#itemCarrousel-3{
    width: 100%;
    height: 100%;
    background-image: url("../img/BocaSeca5.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
