}
#itemCarrousel-4{
    width: 100%;
    height: 100%;
    background-image: url("../img/BocaSeca6.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: rgb(200, 226, 50);
}
#itemCarrousel-5{
    width: 100%;
    height: 100%;
    background-image: url("../img/BocaSeca7.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: rgb(200, 226, 50);
}

.conteCarrouselController{
    width: 50em;
    display: flex;
    justify-content: space-evenly;
   
}

.conteCarrouselController > a{
    text-decoration: none;
    font-size: 4em;
    color: rgb(0, 0, 0);

}

.itemCarrouselArrows > a > i{
    color: white;
    text-shadow: 4px 2px 15px rgb(255, 255, 255);
}
@media(max-width: 1024px){
    .BocaSeca__header{
        
        justify-content: center;
        align-items:center;
    
    }
    .conteCarrousel{
        width: 30em;
        height:30em;
        overflow: hidden;
        border-radius: 50px;
        background: #ebb2b2;
        box-shadow:  20px 20px 60px #c89797,                 -20px -20px 60px #ffcdcd;
        -webkit-border-radius: 50px;
        -moz-border-radius: 50px;
        -ms-border-radius: 50px;
        -o-border-radius: 50px;
    }
    
}
@media (max-width: 480px) {
    .BocaSeca__header-h2{
        width: 20rem;
        background-color: rgba(255, 255, 255,0.3);
        color: rgb(0, 0, 0);
        position: relative;
        z-index: 1;
        padding-right: 1rem;
        font-size: 1.5em;
    }
    .conteCarrousel{
        width: 20em;
        height:30em;
        overflow: hidden;
        border-radius: 50px;
        background: #ebb2b2;
        box-shadow:  20px 20px 60px #c89797,                 -20px -20px 60px #ffcdcd;
        -webkit-border-radius: 50px;
        -moz-border-radius: 50px;
        -ms-border-radius: 50px;
        -o-border-radius: 50px;
    }
    .conteCarrouselController{
        width: 22em;
        display: flex;
        justify-content: space-evenly;
       
    }
}