.Halitosis__header{
    font-size: 1rem;
    width: 100%;
    height: 60vh;
    background-image: url(../img/halitosis1.jpg);
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    position: relative;
    z-index: -1;
    display: flex;
    justify-content: flex-end;
    align-items:center;

}
.Halitosis__header::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(94deg, rgba(252,252,252,0) 37%, rgba(21,103,103,1) 99%);
   

}
.Halitosis__header-h2{
    width: 30rem;
    

    color: white;
    position: relative;
    z-index: 1;
    padding-right: 1rem;
    font-size: 2em;
}
/*---------------*/
.Halitosis__section{
    
    font-size: 1rem;
    width: 100%;
    min-height: 120em;
    display: flex;
    flex-direction: column;
}
.Halitosis__section-card{
    
    width: 100%;
    height:40em;
    overflow: hidden;
    display: flex;flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: #156767;
  
}
.Halitosis__card--text{
    width: 40em;

    margin: 2em;
    padding: 1em;
    border-radius: 50px;
background: #e0e0e0;
box-shadow:  20px 20px 60px #bebebe,
             -20px -20px 60px #ffffff;
}
.Halitosis__text--h2{
    color: red;
    text-transform: capitalize;
}
.Halitosis__text--p{
    padding-top: 1em;
}

.Halitosis__card-img{
    width: 35em;
    overflow: hidden;

}
.Halitosis__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: right;
}
@media(max-width: 1024px){
    .Halitosis__section-card{
        width: 100%;
        min-height:auto;
        overflow: visible;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #156767;
      
    }
    .Halitosis__section{
        font-size: 1rem;
        width: 100%;
        min-height: 120em;
        display: flex;
        flex-direction: column;
    }
    .Halitosis__card-img{
        width: 35em;
        
        
    
    }
    .Halitosis__img{
        width: 0%;
        height: 0%;
        object-fit: cover;
        object-position: right;
    }
    
}
@media (max-width: 480px) {
    .Halitosis__header{
        
        font-size: 1rem;
        width: 100%;
        height: 60vh;
        background-image: url(../img/halitosis1.jpg);
        background-repeat: no-repeat;
        background-position: top;
        background-size: cover;
        position: relative;
        z-index: -1;
        display: flex;
        justify-content: center;
        align-items:center;
    
    }
    .Halitosis__header-h2{
      
        width: 15rem;

       background-color: rgba(255, 255, 255,.4);
        color: rgb(0, 0, 0);
        position: relative;
        z-index: 1;
        padding-right: 0rem;
        font-size: 2em;
    }
   

    .Halitosis__card--text{
        
       
        font-size: .7rem;
    width: 30em;
    overflow: scroll;

    margin: 0em;
    padding: 2em;
    border-radius: 50px;
   
    box-shadow:  20px 20px 60px #bebebe,
                -20px -20px 60px #ffffff;
    }
    .Halitosis__section-card{
        font-size: 1rem;
        width: 100%;
  
        overflow: visible;
